.scroll-wrapper {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 5px; /* Ajout d'un petit padding pour ne pas coller aux bords de Power BI */
}

/* C'EST ICI QUE ÇA SE PASSE */
.accounting-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: flex-start;

    /* --- ARRONDI ET BORDURE --- */
    border-radius: 8px;       /* Coins arrondis */
    overflow: hidden;         /* Coupe ce qui dépasse */
    
    /* MODIFICATION ICI : Noir (0,0,0) avec 25% d'opacité (0.25) */
    border: 1px solid rgba(0, 0, 0, 0.25); 
    
    /* Optionnel : Ombre très légère pour détacher du fond blanc */
    box-shadow: 0 2px 8px rgba(0,0,0,0.05); 
}

.dynamic-column {
    flex: 1;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #ccc;
    min-width: 0;
}

.dynamic-column:last-child {
    border-right: none;
}

table {
    width: 100%;
    border-collapse: collapse;
    font-family: 'Segoe UI', sans-serif;
    table-layout: fixed; /* Important pour que le texte revienne à la ligne */
}

thead th {
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: #003366; 
    color: white;
    text-transform: uppercase;
    font-size: 14px;
    text-align: center;
    padding: 5px; /* Un peu de padding pour les titres */
    min-height: 40px;
    /* border-bottom: 3px solid #eea20e;  <-- J'AI SUPPRIMÉ CETTE LIGNE (Barre Jaune) */
    overflow: hidden;
    white-space: normal; /* Autorise le titre sur 2 lignes si besoin */
}

td {
    padding: 4px 8px;
    border-bottom: 1px solid #eee;
    
    /* --- MODIFICATION POUR TEXTE SUR 1 LIGNE AVEC ... --- */
    white-space: nowrap; 
    overflow: hidden; 
    text-overflow: ellipsis;
    vertical-align: middle;
}